#contact form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;

    @include media-sm {
        font-size: 15px;
    }

    @include media-xs {
        font-size: 14px;
    }
}

#contact label {
    display: none;
}

#contact input,
#contact textarea {
    width: 80%;
    padding: 10px;
    border: 1px solid var(--color-border-grey);
    border-radius: 5px;

    @include media-sm {
        width: 95%;
    }
}

#contact button {
    padding: 10px 20px;
    background-color: var(--color-accent-blue);
    font-family: $font-fourth;
    font-weight: 700;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
        background-color: var(--color-accent-blue-hover);
    }
}

#result {
    text-align: center;
    margin-top: 20px;
    font-size: 1.2em;
    color: var(--color-accent-orange);

    @include media-sm {
        font-size: 1em;
    }

    @include media-xs {
        font-size: 0.8em;
    }
}
